@import './Mixins';
@import './Variables';

.form-container{
    position: relative;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
    .btn-choose-template {
        @include button(200px, 60px);
    }
    .btn-return {
        position: absolute;
        left: 20px;
        top: 0;
        background: none;
        border: none;
        outline: none;
        i {
            font-size: 7rem;
            color: $primaryColor;
            cursor: pointer;
        }
    }
    .card-container {
        position: absolute;
        left: 50%;
        transform: translate(-50%);
    }
    .credit-card-form{
        display: block;
        margin: auto;
        width: 80vw;
        max-width: 600px;
        background-color: $formPrimaryColor;
        border-radius: 5px;
        margin: 20px auto;
        margin-bottom: 300px;
        -moz-box-shadow: 0px 15px 25px rgba(0,0,0,.8);
        -webkit-box-shadow: 0px 15px 25px rgba(0,0,0,.8);
        box-shadow: 0px 15px 25px rgba(0,0,0,.8);
        display: flex;
        z-index: 1;
        margin-top: 200px;
        .inputs-container {
            margin-top: 250px;
            padding: 15px;
            color: #222222;
            font-weight: 500;
            .input-row {
                display: flex;
                justify-content: space-evenly;
                width: 100%;
                .input-column {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    width: 100%;
                    padding: 2px;
                    .card-input-label{
                        margin-bottom: 5px;
                        font-size: 1.5rem;
                        font-family: 'Nunito Sans', sans-serif;
                        font-weight: 600;
                    }
                    input, select{
                        background: none;
                        border: 1px solid #22222240;
                        border-radius: 5px;
                        outline: none;
                        width: 100%;
                        margin: 5;
                        height: 40px;
                        vertical-align: bottom;
                        box-sizing: border-box;
                        -moz-box-sizing: border-box;
                        -webkit-box-sizing: border-box;
                        font-size: 2rem;
                        color: white;
                        font-weight: 600;
                        transition: all 0.4s;
                        font-family: 'Nunito Sans', sans-serif;
                        text-align: left;
                        color: #2d2d2d;   
                        &:focus {
                            border: 1px solid $formSecondaryColor;
                        }
                        select {
                            -webkit-appearance: none;
                            text-align-last:center;
                            cursor: pointer;
                          } 
                        .cardCVV {
                            text-align: center;
                        }
                        .select-box {
                            position: relative;
                            width: 100%;
                        
                        }                      
                    }
                }
            }
            .btn-submit {
                margin: 30px auto;
                padding: 15px;
                width: 100%;
                height: 60px;
                font-size: 28px;
                font-weight: bold;
                border-radius: 5px;
                border: none;
                background-color: $formSecondaryColor;
                outline: none;
                cursor: pointer;
                transition: background-color 0.2s;
                &:active {
                    background-color: $primaryColor;
                }
            }
            
        }
    }
}

input[type=number] {
    -moz-appearance: textfield;
  }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

